import mixins from "~/mixins/output.js";

export default {
    mixins: [mixins],
    data() {
        return {
            weeklyStepData: null,
        };
    },
    async fetch() {
        const { weeklyStepData, weeklyDistanceData } = await this.$axios.$get(
            "/weekly-step-data"
        );
        this.weeklyStepData = weeklyStepData;
    },
};
